import { Clock, LoadingManager } from "three";
import View from "./View";

export class App {
    public view: View;
    public clock: Clock;
    public loadingManager!: LoadingManager

    public progressLoaded = 0

    constructor() {
        const canvasBox = <HTMLCanvasElement>document.getElementById("webgl-canvas");
        this.view = new View(canvasBox);
        this.clock = new Clock(false);
        this.clock.start();
        this.resize()
        this.view.actualView.engine.loadingManager.onLoad = () => {
            this.view.actualView.ready = true
        }
        this.view.actualView.engine.loadingManager.onProgress = (item, loaded, total) => {
            console.log(item)
            console.log(loaded)
            this.progressLoaded = Math.trunc(loaded)
            console.log(this.progressLoaded)
            console.log(total)
        }
        this.update.bind(this)();
        window.addEventListener("resize", this.resize);

    }

    private resize = (): void => {
        this.view.onWindowResize(window.innerWidth, window.innerHeight);
    }

    private update() {
        if (this.view.actualView.ready) {
            this.view.update(this.clock.getElapsedTime() / 1000, this.clock.getDelta());

        }
        requestAnimationFrame(this.update.bind(this));
    }

}
